
import {
	IonContent,
	IonHeader,
	IonPage,
	IonTitle,
	IonToolbar,
	IonButton,
	modalController,
	isPlatform,
} from '@ionic/vue';
import Keuze from './Keuze.vue';
import { defineComponent, ref } from 'vue';

export default defineComponent({
	name: 'Home',
	components: {
		IonContent,
		IonHeader,
		IonPage,
		IonTitle,
		IonToolbar,
		IonButton,
	},
	setup() {
		const page = ref();
		const ios = isPlatform('ios');

		return { page, ios };
	},
	methods: {
		async openKeuze(keuze: string) {
			const modal = await modalController.create({
				component: Keuze,
				cssClass: 'keuze-class',
				swipeToClose: true,
				presentingElement: this.page.$el,
				componentProps: {
					keuze: keuze,
				},
			});
			return modal.present();
		},
	},
});
